.te-collection {
  width: 100%;
  margin: 20px auto;
  background: inherit;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.te-collection-small .te-product-card {
  width: 50%;
  max-width: 300px;
}
.te-collection-medium .te-product-card {
  width: 33%;
  min-width: 250px;
}
.te-collection-large .te-product-card {
  width: 25%;
}
.te-collection-extra-large .te-product-card {
  width: 20%;
}
.te-collection .te-product-card {
  width: 50%;
}
.te-collection .te-product-card .te-button:not(.disabled):hover {
  color: white;
  opacity: 0.8;
  border-color: #222;
}
@media (min-width: 768px) {
  .te-collection .te-product-card {
    width: 33%;
    max-width: 400px;
  }
}
@media (min-width: 960px) {
  .te-collection .te-product-card {
    width: 25%;
  }
}
@media (min-width: 1280px) {
  .te-collection .te-product-card {
    width: 20%;
  }
}
