.te-store {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.te-store .te-store-collection {
  width: 100%;
}
.te-store .te-store-collection .te-collection {
  margin: 0;
}
.te-store .te-store-menu {
  box-sizing: border-box;
  display: block;
  width: 100%;
  overflow-x: auto;
  position: relative;
  padding: 0 20px;
  white-space: nowrap;
  height: auto;
  text-align: center;
  margin-top: 20px;
}
.te-store .te-store-menu::-webkit-scrollbar {
  display: none;
}
.te-store .te-store-menu-item {
  display: inline-block;
  padding: 6px 7px;
  margin: 0 5px;
  color: #333;
  background-color: #f5f5f5;
  text-decoration: none;
  font-weight: 500;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: color 0.4s ease;
}
.te-store .te-store-menu-item-active {
  background-color: #ebebeb;
}
@media (min-width: 768px) {
  .te-store-small .te-store-menu {
    display: flex;
    flex-wrap: wrap;
  }
  .te-store-small .te-store-menu .te-store-menu-item {
    margin: 5px;
  }
}
.te-store-large {
  flex-direction: row;
}
.te-store-large .te-store-menu {
  margin-right: 20px;
  width: auto;
  white-space: normal;
}
.te-store-large .te-store-menu-item {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.7);
  display: block;
  font-weight: 300;
  letter-spacing: 0.5px;
  list-style: none;
  margin: 0;
  padding: 8px 7px;
  text-align: left;
}
.te-store-large .te-store-menu-item-active {
  background-color: rgba(0, 0, 0, 0.03);
}
.te-store-large .te-store-menu-item:hover {
  padding-left: 5px;
  border-left: 2px solid #5f5f5f;
  background-color: rgba(0, 0, 0, 0.01);
}
