.te-product-overlay {
  background: rgba(60, 60, 60, 0.7);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 8000;
  overflow: auto;
}
@media  screen and (min-width: 768px) and (max-width: 979px)  {
  .te-product-overlay .te-product-image {
    height: 60vw;
  }
}
.te-product-card-modal {
  z-index: 9000;
  position: fixed;
  top: 10px;
  width: 90%;
  left: 5%;
  right: 5%;
  overflow: hidden;
  background: white;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-backface-visibility: hidden;
  height: calc(100vh - 20px);
  padding-top: 50px;
  display: block;
}
.te-product-card-modal.display-block {
  display: block;
}
.te-product-card-modal.display-none {
  display: none;
}
.te-product-card-modal-inner {
  overflow: scroll;
  height: 100%;
}
.te-product-card-modal-inner .product {
  margin-top: 0;
  padding-top: 0;
}
.te-product-card-modal-inner .te-product-info-container {
  padding: 0;
}
.te-product-card-modal .te-product-modal-close {
  font-weight: 400;
  position: absolute;
  top: 15px;
  right: 15px;
  width: auto;
  height: auto;
  z-index: 99999;
  cursor: pointer;
  font-size: 20px;
}
@-webkit-keyframes scaleout {
  0% {
    -webkit-transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
@keyframes scaleout {
  0% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
.te-product-card {
  padding: 10px;
  cursor: pointer;
  overflow: hidden;
  box-sizing: border-box;
  font-size: 12px;
}
a.te-product-card:hover {
  color: inherit;
}
.te-product-card.te-product-card-large,
[data-tipser-imgsize='1.2'] .te-product-card {
  width: 50%;
  height: 390px;
}
@media (min-width: 480px) {
  .te-product-card.te-product-card-large,
  [data-tipser-imgsize='1.2'] .te-product-card {
    height: 430px;
  }
}
@media (min-width: 768px) {
  .te-product-card.te-product-card-large,
  [data-tipser-imgsize='1.2'] .te-product-card {
    width: 33%;
    height: 460px;
    max-width: 400px;
  }
}
@media (min-width: 960px) {
  .te-product-card.te-product-card-large,
  [data-tipser-imgsize='1.2'] .te-product-card {
    height: 560px;
    width: 33%;
  }
}
.te-product-card.te-product-card-small,
[data-tipser-imgsize='0.8'] .te-product-card {
  height: 355px;
}
@media (min-width: 480px) {
  .te-product-card.te-product-card-small,
  [data-tipser-imgsize='0.8'] .te-product-card {
    width: 33%;
  }
}
@media (min-width: 768px) {
  .te-product-card.te-product-card-small,
  [data-tipser-imgsize='0.8'] .te-product-card {
    height: 375px;
    width: 25%;
  }
}
@media (min-width: 960px) {
  .te-product-card.te-product-card-small,
  [data-tipser-imgsize='0.8'] .te-product-card {
    height: 440px;
    width: 20%;
    min-width: 0;
  }
}
.te-product-card .te-button {
  line-height: 2.08em;
  margin-top: 1.16em;
  min-width: auto;
}
.te-product-card .te-button-text {
  font-size: 0.9em;
  font-weight: 400;
  letter-spacing: 0.5px;
}
.te-product-card .te-button.disabled {
  border: 1px solid #eee;
}
.te-product-card .te-button.disabled .te-button-body {
  background: #fff;
}
.te-product-card .te-button.disabled .te-button-text {
  color: #ccc;
}
.te-product-card .te-product-discount-label {
  display: none;
}
.te-product-card .te-product-card-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 2.5em;
  padding: 15px 0;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;
  height: auto;
  cursor: default;
}
.te-product-card .te-product-card-img {
  width: 100%;
  height: 195px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 0.3s ease;
}
.te-product-card .te-product-card-img-invalid {
  background-color: #dddddd;
  width: 75%;
  max-width: 225px;
  margin: 0 auto;
  height: 100%;
}
.te-product-card .te-product-card-img:hover {
  opacity: 0.9;
}
@media (min-width: 768px) {
  .te-product-card .te-product-card-img {
    height: 220px;
  }
}
@media (min-width: 960px) {
  .te-product-card .te-product-card-img {
    height: 250px;
  }
}
[data-tipser-imgsize='1.2'] .te-product-card .te-product-card-img {
  height: 235px;
}
@media (min-width: 768px) {
  [data-tipser-imgsize='1.2'] .te-product-card .te-product-card-img {
    height: 265px;
  }
}
@media (min-width: 960px) {
  [data-tipser-imgsize='1.2'] .te-product-card .te-product-card-img {
    height: 360px;
  }
}
[data-tipser-imgsize='0.8'] .te-product-card .te-product-card-img {
  height: 155px;
}
@media (min-width: 768px) {
  [data-tipser-imgsize='0.8'] .te-product-card .te-product-card-img {
    height: 175px;
  }
}
@media (min-width: 960px) {
  [data-tipser-imgsize='0.8'] .te-product-card .te-product-card-img {
    height: 240px;
  }
}
.te-product-card .te-product-card-name {
  font-size: 1em;
  font-weight: normal;
  line-height: 1.3em;
  white-space: nowrap;
  text-overflow: ellipsis;
  letter-spacing: 0px;
  color: #5f5f5f;
  overflow: hidden;
  max-width: 100%;
  text-align: left;
  display: inline-block;
}
.te-product-card .te-product-card-name:hover {
  color: #525860;
}
.te-product-card .te-product-card-brand {
  font-size: 1em;
  cursor: default;
  font-weight: normal;
  letter-spacing: 0;
  color: #999;
  text-transform: uppercase;
}
.te-product-card .te-product-card-price {
  align-items: center;
  color: #5f5f5f;
  display: flex;
  font-size: 1.08em;
  font-weight: 600;
  justify-content: center;
  margin-top: 0.5em;
}
.te-product-card .te-product-card-unit-price {
  color: #afafaf;
  line-height: 1em;
  margin-bottom: 1em;
}
.te-product-card .te-product-card-sales-price {
  color: #cd5550;
  font-weight: 600;
  font-size: 1.08em;
}
.te-product-card .te-product-card-regular-price {
  font-size: 1em;
  font-weight: 400;
  margin-left: 10px;
  text-decoration: line-through;
}
@media (min-width: 960px) {
  .te-product-card {
    padding: 10px 20px;
  }
}
.te-product-card-compact {
  max-width: 300px;
  width: 100%;
  display: block;
  padding: 0;
  margin: 30px auto;
  font-size: inherit;
}
.te-product-card-compact .te-button {
  font-size: 0.8em;
}
.te-product-card-compact .te-product-card-img {
  height: 300px;
}
