.te-button-clear {
  display: inline-flex;
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  height: auto;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: inherit;
  white-space: inherit;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  cursor: pointer;
  text-decoration: none;
  text-align: inherit;
}
