.te-added-to-cart {
  color: #0d0d0d;
  position: fixed;
  top: 4em;
  right: 2em;
  z-index: 9000;
  width: 20em;
  padding: 1em;
  border: 1px solid #333;
  background-color: white;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.1), 0 0 2px rgba(0, 0, 0, 0.3);
}
.te-added-to-cart .te-added-to-cart-header {
  font-size: 18px;
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2em;
}
.te-added-to-cart .te-added-to-cart-header h3 {
  margin: 0;
}
.te-added-to-cart .te-added-to-cart-img-container {
  text-align: center;
  margin-bottom: 2em;
}
.te-added-to-cart .te-added-to-cart-img-container > img {
  max-width: 100%;
  max-height: 16em;
}
.te-added-to-cart .te-added-to-cart-product-basic-info {
  font-size: 14px;
  margin-bottom: 1em;
}
.te-added-to-cart .te-added-to-cart-title {
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .te-added-to-cart {
    top: auto;
    left: 0;
    bottom: 1em;
    right: 0;
    margin: 0 auto;
    width: calc(100% - 4em);
  }
  .te-added-to-cart-header {
    margin-bottom: 1em;
  }
  .te-added-to-cart-img-container {
    display: none;
  }
}
