.cart-icon {
  display: inline-block;
}
.cart-icon.active {
  -webkit-animation-name: pulse;
  -webkit-animation-duration: 1s;
  animation-name: pulse;
  animation-duration: 1s;
}
.tipser-icon-cart {
  font-size: 35px;
  position: relative;
}
.tipser-cart-size-indicator {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: inherit;
  width: 1.5em;
  height: 1.5em;
  top: 50%;
  border-style: solid;
  border-width: 1px;
  border-radius: 50%;
  line-height: 1.4em;
  box-sizing: border-box;
  text-align: center;
  font-size: 0.45em;
  background: #fff;
}
/********
* pulse *
*********/
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
