.te-product-overlay {
  background: rgba(60, 60, 60, 0.7);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 8000;
  overflow: auto;
}
@media  screen and (min-width: 768px) and (max-width: 979px)  {
  .te-product-overlay .te-product-image {
    height: 60vw;
  }
}
.te-product-card-modal {
  z-index: 9000;
  position: fixed;
  top: 10px;
  width: 90%;
  left: 5%;
  right: 5%;
  overflow: hidden;
  background: white;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-backface-visibility: hidden;
  height: calc(100vh - 20px);
  padding-top: 50px;
  display: block;
}
.te-product-card-modal.display-block {
  display: block;
}
.te-product-card-modal.display-none {
  display: none;
}
.te-product-card-modal-inner {
  overflow: scroll;
  height: 100%;
}
.te-product-card-modal-inner .product {
  margin-top: 0;
  padding-top: 0;
}
.te-product-card-modal-inner .te-product-info-container {
  padding: 0;
}
.te-product-card-modal .te-product-modal-close {
  font-weight: 400;
  position: absolute;
  top: 15px;
  right: 15px;
  width: auto;
  height: auto;
  z-index: 99999;
  cursor: pointer;
  font-size: 20px;
}
@-webkit-keyframes scaleout {
  0% {
    -webkit-transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
@keyframes scaleout {
  0% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
