.te-product {
  background-color: #fff;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  margin: 40px auto;
  max-width: 100%;
}
@media (min-width: 480px) {
  .te-product {
    padding: 20px;
    max-width: 1280px;
  }
}
@media (min-width: 768px) {
  .te-product {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}
.te-product .te-button {
  cursor: pointer;
  margin: 10px auto 20px;
  width: 100%;
  max-width: 300px;
}
@media (min-width: 768px) {
  .te-product .te-button {
    margin: 0 0 20px;
    max-width: 250px;
  }
}
.te-product .te-button .te-button.disabled {
  color: #525860;
  background: #dddddd;
  cursor: not-allowed;
}
.te-product .te-button .added-to-cart {
  background-color: #437a94;
}
.te-product .tipser-icon {
  width: 25px;
  display: inline-block;
  font-size: 17px;
}
.te-product .te-dropdown-container {
  margin: 0 0 20px;
}
.te-product .te-product-image {
  height: auto;
  position: relative;
  margin: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .te-product .te-product-image {
    width: 60%;
    text-align: right;
  }
  .te-product .te-product-image-wrapper {
    height: 600px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .te-product .te-product-image-wrapper img {
    max-width: 100%;
    max-height: 100%;
  }
}
.te-product .te-product-thumbnails {
  align-items: center;
  display: none;
  justify-content: center;
  padding-top: 20px;
}
@media (min-width: 768px) {
  .te-product .te-product-thumbnails {
    display: flex;
  }
}
.te-product .te-product-thumb {
  width: calc((100% / 6) - 10px);
  min-width: 30px;
  position: relative;
  margin: 0 5px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .te-product .te-product-thumb {
    max-height: 100px;
    min-width: 50px;
  }
}
.te-product .te-product-thumb img {
  max-width: 100%;
  max-height: 100%;
  height: 100px;
}
.te-product .te-product-details {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 100%;
  letter-spacing: 1px;
  margin-top: 30px;
  padding: 0 15px;
}
@media (min-width: 768px) {
  .te-product .te-product-details {
    padding-left: 30px;
    justify-content: center;
    margin-top: 0;
    width: 40%;
  }
}
@media (min-width: 960px) {
  .te-product .te-product-details {
    margin-top: 20px;
    padding-left: 50px;
    justify-content: center;
  }
}
@media (min-width: 1280px) {
  .te-product .te-product-details {
    margin-top: 20px;
    padding-left: 100px;
    padding-right: 100px;
  }
}
.te-product .te-product-basic-info {
  margin-bottom: 25px;
  text-align: center;
}
@media (min-width: 768px) {
  .te-product .te-product-basic-info {
    text-align: left;
  }
}
.te-product .te-product-name {
  font-weight: 700;
  font-size: 1.3em;
  line-height: 1.3em;
  margin-bottom: 3px;
  text-transform: uppercase;
}
.te-product .te-product-brand {
  font-size: 0.8em;
  margin-bottom: 20px;
  text-transform: uppercase;
}
.te-product .te-product-sales-price {
  color: #cd5550;
  font-weight: bold;
  font-size: 1.1em;
}
.te-product .te-product-regular-price {
  text-decoration: line-through;
  font-size: 0.8em;
}
.te-product .te-product-unit-price {
  color: #afafaf;
  margin-top: 0.5em;
}
.te-product .te-product-info-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto 20px;
  width: 100%;
  max-width: 300px;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .te-product .te-product-info-container {
    margin: 0 0 20px;
    max-width: 250px;
  }
}
.te-product .te-product-usp {
  list-style-type: none;
  padding: 0;
  display: block;
}
.te-product .te-product-usp-item {
  letter-spacing: 0.08em;
  line-height: 1.6;
  margin: 0 0 0 35px;
}
.te-product .te-product-usp-item .tipser-icon {
  margin-left: -35px;
  color: #668f67;
}
.te-product .te-product-info {
  font-size: 0.8em;
  margin-left: 10px;
  letter-spacing: 0;
}
.te-product .te-product-description {
  cursor: pointer;
  border-top: 1px solid #bdc3c7;
  border-bottom: 1px solid #bdc3c7;
}
.te-product .te-product-description-title {
  text-transform: uppercase;
  display: block;
  font-size: 0.85em;
  margin: 10px;
}
.te-product .te-product-description-content {
  font-size: 0.85em;
  line-height: 1.7;
  overflow-y: auto;
  transition: 1s;
  max-height: 0;
  margin: 0 10px;
  letter-spacing: 0;
}
.te-product .te-product-description-content.active {
  max-height: 200px;
  margin: 10px;
}
.te-product .te-product-description-content p {
  font-size: inherit;
  margin: 5px 0;
}
.te-product .te-product-placeholder {
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  text-transform: uppercase;
  text-align: center;
  padding: 40px;
}
.slick-dots li {
  margin: 0;
}
.slick-dots li button:before {
  font-size: 10px;
}
button.slick-prev,
button.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}
.slick-arrow {
  z-index: 1;
}
.slick-prev {
  left: 0;
}
.slick-prev:before {
  content: '\e927';
}
.slick-next {
  right: 0;
}
.slick-next:before {
  content: '\e929';
}
.slick-next:before,
.slick-prev:before {
  font-family: 'Tipser-font';
  color: black;
}
.te-single-product {
  max-height: 400px;
  box-sizing: border-box;
  padding: 0;
  margin: 40px auto;
}
.te-single-product .te-button {
  max-height: 25px;
  line-height: 25px;
  min-height: 25px;
  width: auto !important;
  margin-bottom: 10px;
}
.te-single-product .te-product-usp {
  margin: 0;
}
.te-single-product .tipser-icon {
  font-size: 1em;
}
.te-single-product .te-dropdown-container {
  margin: 0 0 10px;
}
.te-single-product .te-dropdown__control {
  top: 0;
  min-height: 25px;
}
.te-single-product .te-dropdown__value-container {
  padding: 0 10px;
  line-height: 25px;
  height: 25px;
  max-height: 25px;
}
.te-single-product .te-product-image-wrapper,
.te-single-product .te-product-image-wrapper img {
  max-height: 290px;
}
.te-single-product .te-product-thumbnails {
  max-height: 100px;
  padding-top: 10px;
}
.te-single-product .te-product-thumb {
  max-height: 90px;
  width: auto;
}
.te-single-product .te-product-thumb img {
  max-height: 90px;
}
.te-single-product .te-product-details {
  padding: 0;
  position: relative;
}
.te-single-product .te-product-basic-info {
  margin-bottom: 10px;
}
.te-single-product .te-product-name {
  font-size: 1em;
}
.te-single-product .te-product-brand {
  font-size: 0.6em;
  margin-bottom: 10px;
}
.te-single-product .te-product-info-container {
  margin-bottom: 10px;
  text-align: left;
  padding: 0 5px;
}
.te-single-product .te-product-usp {
  margin: 0;
}
.te-single-product .te-product-description {
  background: white;
}
.te-single-product .te-product-description.active {
  box-shadow: 3px 3px 12px rgba(0, 0, 0, 0.4);
}
.te-single-product .te-product-description-title {
  font-size: 0.7em;
  margin: 5px;
}
.te-single-product .te-product-description-content {
  font-size: 0.6em;
}
.te-single-product .te-product-description-content ul,
.te-single-product .te-product-description-content ol {
  text-align: left;
}
.te-single-product__column {
  max-width: 100%;
  max-height: initial;
  align-items: center;
  width: 100%;
  margin: 30px auto;
  flex-direction: column;
}
.te-single-product__column .slick-dots li button:before {
  font-size: 6px;
}
.te-single-product__column .te-product {
  margin: 10px auto;
}
.te-single-product__column .te-product-image-wrapper,
.te-single-product__column .te-product-image-wrapper img {
  width: auto !important;
  display: block !important;
  margin: 0 auto;
}
.te-single-product__column .te-product-thumbnails {
  display: none;
}
.te-single-product__column .te-product-details {
  margin: 0;
  text-align: center;
  width: 400px;
}
.te-single-product__column .te-product-basic-info {
  text-align: center;
}
.te-single-product__column .te-product-info-container {
  margin: 0 auto;
}
