.te-button {
  cursor: pointer;
  border: none;
  border-radius: 0;
  display: inline-block;
  overflow: visible;
  font-size: 12px;
  font-weight: 200;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  line-height: 4em;
  margin: 0;
  min-height: 30px;
  text-decoration: none;
  text-align: center;
  box-sizing: border-box;
  transition: 0.3s;
  min-width: 200px;
  padding: 0;
  position: relative;
  vertical-align: middle;
  -webkit-appearance: none;
  z-index: 0;
}
.te-button-body {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.te-button-body:hover {
  opacity: 0.8;
}
.te-button-text {
  padding: 0 40px;
  color: #fff;
  line-height: 30px;
  white-space: nowrap;
}
.te-button-outline {
  background-color: transparent;
  border: 1px solid #333;
  color: #333;
}
.te-button-outline:hover {
  color: #fff;
}
.te-buy-button-small {
  border-radius: 0;
  padding: 10px 30px;
  position: relative;
  text-transform: uppercase;
  text-align: center;
  transition: 0.5s;
  -webkit-backface-visibility: hidden;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.8;
  display: inline-block;
  margin: 5px auto 0;
  width: 100%;
}
.te-button-small {
  min-height: 14px;
  padding: 2px 19px;
  font-size: 10px;
  line-height: 2;
}
.te-button-medium {
  font-size: 12px;
  padding: 4px 30px;
  line-height: 3;
  min-height: 20px;
}
