.te-dropdown-container {
  margin: 0 auto 20px;
  width: 100%;
  max-width: 300px;
}
@media (min-width: 768px) {
  .te-dropdown {
    margin: 0 0 20px;
    max-width: 250px;
  }
}
.te-dropdown-validation-error {
  border: 2px solid #cd5550;
}
.te-dropdown div {
  outline: none;
  box-shadow: none;
  border-color: #333;
}
.te-dropdown-select {
  border: 1px solid #333;
  background-color: transparent;
  border-radius: 0;
  font-size: 0.7em;
  padding: 7px 10px;
  position: relative;
}
.te-dropdown-select .tipser-icon-small-arrow-down {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}
.te-dropdown-select select {
  width: 100%;
  height: 100%;
  letter-spacing: 1px;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: 0;
  background: transparent;
}
.te-dropdown-select select:active,
.te-dropdown-select select:hover,
.te-dropdown-select select:focus {
  outline: 0;
}
